"use client";

import { cn } from "@/lib/utils";
import Link from "next/link";
import { usePathname } from "next/navigation";

const navList = [
  { name: "post", href: "/post" },
  { name: "about", href: "/about" },
];
const localePathList = ["/about"];

const Header = () => {
  const pathname = usePathname();

  const isLocation = localePathList.some((path) => pathname.startsWith(path));

  console.log(isLocation, navList);
  return (
    <header className="flex mt-2 mb-5 md:mb-10 items-center relative">
      <nav className="fixed flex flex-col">
        <div className="mt-1 flex h-[64px] w-full max-w-[1200px] items-center justify-content">
          <div className="flex items-center font-midium">
            {navList.map((navItem) => (
              <Link
                href={navItem.href}
                key={navItem.name}
                className={cn(
                  "rounded-full px-4 py-1 text-center text-sm transition-colors hover:text-primary",
                  pathname.startsWith(navItem.href)
                    ? "bg-muted font-midium text-primary"
                    : "text-muted-foreground"
                )}
              >
                {navItem.name}
              </Link>
            ))}
          </div>
        </div>
      </nav>
    </header>
  );
};

export default Header;
